"use client"

import { useState } from "react"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Button } from "@/components/ui/button"
import { DatePicker } from "@/components/ui/date-picker"
import SchoolSelect from "@/components/dropdown/dropdown"

interface StudentForm {
  student_name: string
  AdmissionNo: string
  DateOfBirth: string
  AdmissionDate: string
  DateOfLeaving: string
  mobile: string
  email: string
  SchoolName: string
}

const defaultData: StudentForm = {
  student_name: "",
  AdmissionNo: "",
  DateOfBirth: "",
  AdmissionDate: "",
  DateOfLeaving: "",
  mobile: "",
  email: "",
  SchoolName: "",
}

export function StudentRegisterForm() {
  const [formData, setFormData] = useState<StudentForm>(defaultData)

  const handleChange = (key: keyof StudentForm, value: string) => {
    setFormData((prev) => ({ ...prev, [key]: value }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log("Register Student Payload:", formData)
    // ðŸ”— call addStudentApi here
  }

  return (
    <form
      onSubmit={handleSubmit}
      className="grid grid-cols-1 md:grid-cols-2 gap-4"
    >
      {/* School */}
      <div>
        <Label>School</Label>
        <SchoolSelect
          value={formData.SchoolName}
          onChange={(val) => handleChange("SchoolName", val)}
        />
      </div>

      <div>
        <Label>Student Name</Label>
        <Input
          value={formData.student_name}
          onChange={(e) => handleChange("student_name", e.target.value)}
        />
      </div>

      <div>
        <Label>Admission No</Label>
        <Input
          value={formData.AdmissionNo}
          onChange={(e) => handleChange("AdmissionNo", e.target.value)}
        />
      </div>

      <div>
        <Label>Date of Birth</Label>
        <DatePicker
          value={formData.DateOfBirth}
          onChange={(val) => handleChange("DateOfBirth", val)}
        />
      </div>

      <div>
        <Label>Admission Date</Label>
        <DatePicker
          value={formData.AdmissionDate}
          onChange={(val) => handleChange("AdmissionDate", val)}
        />
      </div>

      <div>
        <Label>Date of Leaving</Label>
        <DatePicker
          value={formData.DateOfLeaving}
          onChange={(val) => handleChange("DateOfLeaving", val)}
        />
      </div>

      <div>
        <Label>Mobile</Label>
        <Input
          value={formData.mobile}
          onChange={(e) => handleChange("mobile", e.target.value)}
        />
      </div>

      <div>
        <Label>Email</Label>
        <Input
          value={formData.email}
          onChange={(e) => handleChange("email", e.target.value)}
        />
      </div>

      <div className="md:col-span-2 flex justify-end mt-4">
        <Button type="submit">Register Student</Button>
      </div>
    </form>
  )
}
